import React,{useState,useEffect} from 'react';
import { useSelector, useDispatch } from 'react-redux'
import {
  CCreateElement,
  CSidebar,
  CSidebarBrand,
  CSidebarNav,
  CSidebarNavDivider,
  CSidebarNavTitle,
  CSidebarMinimizer,
  CSidebarNavDropdown,
  CSidebarNavItem,
  CImg,
} from '@coreui/react'

import CIcon from '@coreui/icons-react'

//import SampleData from './SampleData'
import API from 'src/services';
import Navigation from './_nav';
//import { array } from 'prop-types';

const TheSidebar = () => {
const {sidebarShow} = useSelector(state => state.sidebarReducer)
  const dispatch = useDispatch()


  const [dataMenu,setDataMenu] = useState([]);


  const Menu = ()=> {
    let sessionIdUser = sessionStorage.getItem('idUser');
    API.getMenu(sessionIdUser).then(result =>{
        const menuData = result.data;
        const parentId = null;
       // var childRecursive = getNestedChildren(menuData, parentId);
         return setDataMenu(menuData)
    })
  }

  const getNestedChildren = (arr, parentId) =>{

      let arrTmpMenu = [

        {
          _tag: 'CSidebarNavItem',
          name: 'Dashboard',
          to: '/dashboard',
          icon: <CIcon name="cil-speedometer" customClasses="c-sidebar-nav-icon"/>,
          badge: {
            color: 'info',
            text: 'NEW',
          }
        }
      ];

      arr.filter(menuParent => menuParent.idApplTaskParent === null).map(value => {

          let childTmpArr = [];
              arrTmpMenu.push(
                {
                  "_tag": "CSidebarNavDropdown",
                  "name" : value.applTaskName,
                  "route"  : "",
                  "icon": 'cil-puzzle',
                  "_children" : childTmpArr

                });

          arr.map(vChild => {
            if(vChild.idApplTaskParent === value.idApplTask){

                let childItem = arr.filter(filter2 => filter2.idApplTaskParent == vChild.idApplTask);

                if(childItem.length > 0){
                  let tmpChild3 = [];
                  childItem.map(val => {
                      tmpChild3.push( {
                          "_tag": 'CSidebarNavItem',
                          "name": val.applTaskName,
                          "to": `/${val.controllerName}/${val.actionName}`,
                      });
                  })
                  childTmpArr.push( {
                        "_tag": 'CSidebarNavDropdown',
                        "name": vChild.applTaskName,
                         "to": `/${vChild.controllerName}/${vChild.actionName}`,
                        "icon": 'cil-puzzle',
                        "_children": tmpChild3
                  });

                }else{
                   childTmpArr.push( {
                        "_tag": 'CSidebarNavItem',
                        "name": vChild.applTaskName,
                         "to": `/${vChild.controllerName}/${vChild.actionName}`,
                  });
                }


            }
          });


      })



      return arrTmpMenu;
  }

  useEffect(() => {

    Menu();

  },[]);

  return (
    <CSidebar
      show={sidebarShow}
      onShowChange={(val) => dispatch({type: 'SET', payload: val })}
    >
      <CSidebarBrand className="d-md-down-none" to="/">
        <CImg src={process.env.PUBLIC_URL + '/logo.png'} style={{height: 35}} className="c-sidebar-brand-full" />
        <CImg src={process.env.PUBLIC_URL + '/logo-only.png'} style={{height: 35}} className="c-sidebar-brand-minimized" />
      </CSidebarBrand>
      <CSidebarNav>

        <CCreateElement
          items={dataMenu}
          components={{
            CSidebarNavDivider,
            CSidebarNavDropdown,
            CSidebarNavItem,
            CSidebarNavTitle
          }}
        />
      </CSidebarNav>
      <CSidebarMinimizer className="c-d-md-down-none"/>
    </CSidebar>
  )
}

export default React.memo(TheSidebar)
