import axios from 'axios';

const rootAPI = process.env.REACT_APP_ROOT_API
/* const headers = {
                  "Content-type":"application/json"
                }; */

const Get = (pathApi) => {

    const promise = new Promise((resolve, reject) =>{

        axios.get(`${rootAPI}${pathApi}`)
        .then((result) =>{
          resolve(result.data);
        }, (error) =>{
          reject(error)
        })
    })

    return promise;
}


const getMenu = () => Get('UserApps/GetMenusByIdUser?IdUser=1');

const API = {
    getMenu
}

export default API;
